import {updateObjectInArray} from "../utils/object-helpers";
import {ComposersType} from "../Types/Types";

const SELECT_COMPOSER = 'SELECT_COMPOSER';

let initialState = {
    composers: [
    {id: 0, name: 'Johann Sebastian Bach', isLike: true, sample: 'https://mp3store.net/download.php?file=eyJpZCI6IjE4MTQzNSIsInVybCI6ImpvaGFubi1zZWJhc3RpYW4tYmFjaC10b2thdGEtYW5kLWZ1Z2EtcmUtbWlub3JlIiwidHlwZSI6ImxvYWQifQ=='},
    {id: 1, name: 'Wolfgang Amadeus Mozart', isLike: false, sample: 'https://mp3store.net/download.php?file=eyJpZCI6IjExMjMyMCIsInVybCI6IndvbGZnYW5nLWFtYWRldXMtbW96YXJ0LWFsbGVncm8tbW9kZXJhdG8iLCJ0eXBlIjoibG9hZCJ9'},
    {id: 2, name: 'Ludwig van Beethoven', isLike: false, sample: 'https://mp3store.net/download.php?file=eyJpZCI6Ijg4NDgxIiwidXJsIjoibHVkd2lnLXZhbi1iZWV0aG92ZW4tbHVubmF5YS1zb25hdGEtdi1zb3ZyZW1lbm5veS1vYnJhYm90a2UiLCJ0eXBlIjoibG9hZCJ9'}
] as Array<ComposersType>
}
type InitialStateActionType = typeof initialState

const musicReducer = (state = initialState, action:any): InitialStateActionType => {

    switch (action.type) {
        case SELECT_COMPOSER:
            return {
                ...state, composers:
                 state.composers.map(u => {
                    if (u.id === action.id) {
                        return {...u, isLike: !u.isLike}
                    } else {
                        return {
                            ...u, isLike: false
                        }
                    }
                    return u
                })
            };

        default:
            return state;
    }
}

export const selectComposer = (id:number) => ({type: SELECT_COMPOSER, id});

export default musicReducer;